body{
  background: #d1defa;
  font-family: sans-serif, Arial, Helvetica;
  text-align: center;
  padding: 0px;
  margin: 0px;
}
.layout{
  width: 100%;
  height: auto;
  display: grid;
  grid-template-areas: 
      "header header header header"
      "nav nav nav nav"
      "content content content lateral"
      "footer footer footer footer";
  ;
  grid-template-rows: 100px 70px auto 70px;

}


.header{
  grid-area: header;
  background: white;
  text-align: left;
  font-style: italic;
  display: flex;
  justify-content: center;
}

.header h1{
  margin-left: 15px;
  margin-top: 30px;
}


.logo{
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%);
  width: 40px;
  height: 40px;
  border-radius: 300px;
  margin-top: 30px;
}

.play{
  width: 0;
  height: 0;
  margin-top: 15px;
  margin-left: 16px;
  border-left: 12px solid white;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}

.nav{
  grid-area: nav;
  background: linear-gradient(90deg, #03228f 10%, #0e73e4 100%);

}

.nav > ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;

}

.nav li{
  padding: 10px;
}

.nav a{
  display: block;
  margin-left: 30px;
  margin-right: 30px;
  font-size: 18px;
  font-weight: lighter;
  color: white;
  text-decoration: none;
  transition: all 300ms;
}

.nav a:hover{
  transform: scale(1.1, 1.1);
  
}


/* CONTENIDO PRINCIPAL */
.content{
  grid-area: content;
  background: #f6f7f9;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.peli-item{
  flex-grow: 1;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;
  margin: 15px;
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 25px;
  padding-right: 25px;
}

.peli-item .edit {
  margin-right: 7px;
}

button, input[type="submit"],
input[type="button"]{
  width: 100px;
  background: linear-gradient(160deg, #03228f 0%, #0b70e1 77%);
  color: white;
  border: none;
  outline: 0;
  border-radius: 3px;
  padding: 15px;
  margin-top: 15px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 300ms;
  text-transform: capitalize;
}

.peli-item .edit:hover {

  background: #e77b00;
}

.peli-item .delete:hover {
  background: #9b0202;
}

/* LATERAL */

.lateral {
  grid-area: lateral;
  background: #f6f7f9;
  border-left: 1px solid #ddd;
  padding: 20px;

  
}

.lateral > div{
  box-sizing: border-box;
  width: 90%;
  margin: 20px;
  padding: 20px;
  padding-top: 10px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 0px 6px #ddd;

}

.lateral input[type="text"],
.lateral input[type="text"],
.lateral textarea{
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
  border-color: transparent;
}

form>textarea {
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
  border-color: transparent;
}

form>input[type="text"]{
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  padding: 12px;
  border-radius: 5px;
  background: #f6f7f9;
  color: #454545;
  width: 80%;
  opacity: 4;
  outline: 0;
  border-color: transparent;
}

.edit_form{
  margin-top: 16px;
  border-top: lightgrey 1px solid;
}

.footer{
  grid-area: footer;
  background: #d1defa;
  color: #454545;
  border: 1px solid white;
  text-align: left;
  line-height: 68px;
  padding-left: 25px;
}

.footer a{
  color: #0b468a;
  text-decoration: none;
}

@media (max-width: 1085px){
  .content{
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
  }
  .peli-item{
      width: 80%;
  }
  /* .layout{
      grid-template-areas: 
      "header header header header"
      "nav nav nav nav"
      "content content content content"
      "lateral lateral lateral lateral"
      "footer footer footer footer";
  } */
}

@media (max-width: 700px){
  .layout{
      grid-template-areas: 
      "header header header header"
      "nav nav nav nav"
      "lateral lateral lateral lateral"
      "content content content content"
      "footer footer footer footer";
      grid-template-rows: 100px 70px auto auto 70px;
  }
  .content{
      flex-flow: row wrap;
      justify-content: center;
      align-items: center;
      height: auto;
  }
  
}

.no-encontrado{
  color: #9b0202;
}